.friendMessage {
    align-items: start;
    display: flex;
    flex-direction: column;


}

.friendImageAndText {
    align-items: flex-end;
    display: flex;
    gap: 10px;

}

.friendImageAndText img {
    height: 48px;
    width: 48px;
}

.friendText {

}

.friendText:after {

}

.friendName {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: black;
}

.friendMessageText {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
}

.friendTime {
    display: flex;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
    position: relative;
    left: 10px;
    top: 5px;

}
