.sendForm {
    display: flex;
    justify-content: space-between;
    align-items: center;
padding: 100px 60px 0 60px;

}

.textarea {
    background-color: #f5f7fb;
    border: none;
    border-radius: 30px;
    box-sizing: border-box;
    display: flex;
    overflow: hidden;
    padding: 10px 30px;
    position: relative;
    resize: none;
    width: 1000px;
    font-family: Montserrat,sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.6);
    opacity: 0.9;
}

.button {
    background: #0066CC;
    border-radius: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    width: 120px;
    height: 40px;
    border: none;


    color: #FFFFFF;
}

