.message {
    align-items: flex-end;
    display: flex;
    flex-direction: column;
    padding-top: 20px;


}

.imageAndText {
    align-items: flex-end;
    display: flex;
    flex-direction: row-reverse;


}

.imageAndText img {
    height: 48px;
    width: 48px;

}

.text {
    align-items: flex-end;
    background: #06c;
    border-radius: 10px;
    color: #fff;
    display: flex;
    flex-direction: column;
    font-family: Montserrat,sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    justify-content: flex-end;
    line-height: 22px;
    margin-right: 12px;
    max-height: 150px;
    max-width: 600px;
    padding: 8px 13px;
    position: relative;
}

.text:after {
    border-bottom: 20px solid #06c;
    border-right: 20px solid transparent;
    bottom: 0;
    content: "";
    height: 0;
    position: absolute;
    right: -10px;
    width: 0;
    z-index: -1;
}

.name {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
}

.messageText {
    color: #fff;
    display: flex;
    font-family: Montserrat,sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    margin-top: 5px;
}

.time {
    display: flex;
    justify-content: end;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
    position: relative;
    right: 15px;
    top: 5px;



}